import React, { useState, useMemo } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text'
import Button from '../components/Button'
import Dropdown from '../components/Dropdown'
import Pagination from '../components/Pagination'
import { responsive } from '../components/ThemeProvider/theme';

import SheetDataTable from './SheetDataTable'
import PageControl from './PageControl'
import Downloader from './Downloader'

import useGapi from '../contexts/gapi/useGapi'
import { size } from 'lodash'
import CompareSummary from './CompareSummary';

const cleanSheetData = (data) => {
  const header = data[0].map((h, i) => data.some(d => d[i]))
  return data.map((r) => r.filter((d, i) => header[i]))
}

// const colorKeys = [{}, {}]
// const getColor = (name, i = 0) => {
//   const keys = colorKeys[i]
//   if (typeof keys[name] !== 'number') {
//     keys[name] = (max(Object.values(keys)) || 0) + 1
//   }
//   return keys[name]
// }

const HomePage = () => {
  const {
    isSignedIn,
    loadSheets,
    loading,
    sheets,
    AuthButton,
    loadSheetData,
    sheetData,
    SpreadsheetIdInput,
  } = useGapi()
  const [selectedSheet, setSheet] = useState('')
  const [compareSheet, setCompareSheet] = useState('')
  const [downloading, setDownloading] = useState()
  const [startYear, setStartYear] = useState(new Date().getFullYear())
  // const [selectedRow, setSelectedRow] = useState({})
  const [page, setPage] = useState(0)

  const data = useMemo(() => {
    if (sheetData && sheetData[selectedSheet]) {
      return cleanSheetData(sheetData[selectedSheet])
    }
    return null
  }, [sheetData, selectedSheet])
  const compareSheetData = useMemo(() => {
    return compareSheet && sheetData[compareSheet] && cleanSheetData(sheetData[compareSheet])
  }, [sheetData, compareSheet])
  const doubleHeader = useMemo(() => data && data[0].some(h => !h), [data])
  const headerRow = useMemo(() => {
    if (!data) return []
    if (doubleHeader) {
      const colLength = data[0].length
      let groupMemo = []
      // 空白代表要group ['a', 'b', '', 'c']
      // b後面的空白要跟b group一起，所以先反轉陣列，從後面檢查回來，只要是空白，就等到下一個值出現然後把他們group起來
      return data[0].slice(0).reverse().reduce((all, h, i) => {
        // 檢查有沒有值
        if (h) {
          // 檢查有沒有之前先標記要變成group的
          if (groupMemo.length) {
            // if true 把現在這一格也加入group
            groupMemo.push(i)
            all.push({
              Header: h,
              columns: groupMemo.slice().reverse().map(j => ({
                Header: data[1][colLength - j - 1],
                accessor: d => d[colLength - j - 1],
                id: `${i}-${j}`,
              })),
            })
            // 清空group的標記
            groupMemo = []
          } else {
            // if false 就是一般的header
            all.push({ Header: h, accessor: d => d[colLength - i - 1], id: i })
          }
        } else {
          // 沒有值，之後要跟別人group，先暫存起來
          groupMemo.push(i)
        }
        return all
      }, [])
      .reverse() // 最後記得再反轉回來
      .slice(1) // 然後去掉Title
    }
    return data[0].slice(1) // 同樣去掉Title
  }, [data, doubleHeader])
  const tableData = useMemo(() => data ? data.slice(doubleHeader ? 2 : 1) : [], [data, doubleHeader])
  const pages = useMemo(() => {
    return tableData.reduce((all, row) => {
      let i = all.length - 1
      if (row[0]) {
        i = all.push([]) - 1
      }
      all[i].push(row)
      return all
    }, [])
  }, [tableData])

  const compareData = useMemo(() => (compareSheetData) ? compareSheetData.slice(doubleHeader ? 2 : 1) : [], [compareSheetData, doubleHeader])
  const [compareMark, comparePages] = useMemo(() => {
    if (!size(compareData)) return [null, null]
    let page = 0
    return compareData.reduce(([all, pages], row) => {
      if (row[0]) page += 1
      all[row[1]] = row
      pages[row[1]] = page
      return [all, pages]
    }, [{}, {}])
  }, [compareData])
  return (
    <Container py={responsive(0, '2em')}>
      {isSignedIn ? (
        <>
          <Flex>
            <SpreadsheetIdInput flex="1" />
            <Box ml="1em">
              <Button disabled={loading} my="1em" onClick={loadSheets}>{loading ? 'Loading...' : '載入資料表'}</Button>
            </Box>
          </Flex>
          {sheets && (
            <>
              <Flex>
                <Dropdown
                  width={1 / 2}
                  label="選擇資料表"
                  onChange={(selected) => {
                    if (selected) {
                      setSheet(selected.value)
                      setCompareSheet('')
                      loadSheetData(selected.value)
                    } else {
                      setSheet('')
                    }
                  }}
                  disabled={loading}
                  options={sheets.map(sheet => sheet.title)}
                  value={selectedSheet}
                />
                <Dropdown
                  width={1 / 2}
                  label="對比資料表"
                  onChange={(selected) => {
                    if (selected) {
                      setCompareSheet(selected.value)
                      loadSheetData(selected.value)
                    } else {
                      setCompareSheet('')
                    }
                  }}
                  options={sheets.map(sheet => sheet.title).filter(title => title !== selectedSheet)}
                  disabled={loading || !selectedSheet}
                  value={compareSheet}
                />
              </Flex>
              <Tabs style={{ marginTop: '2em' }}>
                <TabList>
                  <Tab>簡報產生器</Tab>
                  <Tab disabled={!compareData.length}>更新對照表</Tab>
                </TabList>
                <TabPanel>
                  <Flex as="label" my="1em" alignItems="center">
                    <Text mr="0.5em">起始年分</Text>
                    <Box width="12em">
                      <input
                        style={{ lineHeight: 2 }}
                        type="number"
                        value={startYear}
                        onChange={e => setStartYear(e.target.value)}
                      />
                    </Box>
                  </Flex>
                  {pages && pages[page] && (
                    <>
                      <SheetDataTable
                        data={pages[page]}
                        headerRow={headerRow}
                        // onSelectRow={setSelectedRow}
                      />
                      <Pagination
                        pageCount={pages.length}
                        pageRangeDisplayed={5}
                        marginPagesDisplayed={2}
                        onPageChange={({ selected }) => setPage(selected)}
                      />
                      <PageControl
                        key={selectedSheet}
                        allData={tableData}
                        headerRow={headerRow.slice(0, headerRow.length - 2)}
                        page={page}
                        data={pages[page]}
                        compareMark={compareMark}
                        sheetName={selectedSheet}
                        startYear={startYear}
                      />
                      <Button onClick={() => setDownloading(true)} disabled={downloading}>
                        批次下載{downloading ? '中...' : ` (${pages.length}頁)` }
                      </Button>
                      {downloading && (
                        <Downloader
                          allData={tableData}
                          headerRow={headerRow.slice(0, headerRow.length - 2)}
                          pages={pages}
                          compareMark={compareMark}
                          sheetName={selectedSheet}
                          startYear={startYear}
                          onDownloaded={() => setDownloading(false)}
                        />
                      )}
                    </>
                  )}
                </TabPanel>
                <TabPanel>
                  <CompareSummary
                    data={pages}
                    headerRow={headerRow}
                    compareMark={compareMark}
                    sheetName={selectedSheet}
                    comparePages={comparePages}
                  />
                </TabPanel>
              </Tabs>
            </>
          )}
        </>
      ) : (
        <>
          <Text>請先登入</Text>
          <AuthButton />
        </>
      )}
    </Container>
  )
}

export default HomePage
